<template>
  <div>
    <template v-for="(t, idx) of title">
      <span :key="idx + '-span1'" class="words" @click="handleTitle(idx)">
        {{ t }}
      </span>
      <span :key="idx + '-span2'" v-if="title.length > 1 && idx !== title.length - 1" style="cursor: default;">
        &nbsp;/&nbsp;
      </span>
    </template>
  </div>
</template>
<script>
export default {
  name: 'routeTitle',
  data () {
    return {
      title: ['HOME']
    }
  },
  watch: {
    '$route': {
      handler (value) {
        this.setTopTitle(value)
      }
    }
  },
  mounted () {
    this.setTopTitle(this.$route)
  },
  methods: {
    setTopTitle (route) {
      this.title = this.getTitleList(route)
    },
    getTitleList (route) {
      const value = route.name
      const routeName = this.routeName
      switch (value) {
        case routeName.home:
          return ['Home']
        case routeName.post:
          return ['Home', route.params.category, route.params.title.toUpperCase()]
        case routeName.category:
          return ['Home', 'Category']
        case routeName.tag:
          return ['Home', 'Tag']
        case routeName.tags:
          return ['Home', 'Tag', route.params.tag]
        case routeName.achieve:
          return ['Home', 'Achieve']
        case routeName.about:
          return ['Home', 'About']
      }
    },
    handleTitle (idx) {
      if (idx === this.title.length - 1) {
        return
      }
      const routeName = this.title[idx]
      if (this.title.length === 3 && idx === 1) {
        if (this.$route.name === this.routeName.tags) {
          this.$router.push({ name: this.routeName.tag })
        } else {
          this.$router.push({ name: this.routeName.category })
        }
      } else {
        this.$router.push({ name: routeName.toLowerCase() })
      }
    }
  }
}
</script>
<style scoped>
.words{
  cursor: pointer;
}
.words:hover{
  color: #D2603A;
}
</style>



