<template>
  <el-container>
    <el-aside style="width: 20.3rem;">
      <side class="side" id="side-out"/>
    </el-aside>
    <el-container>
      <el-header style="height: 3rem;">
        <top-bar/>
      </el-header>
      <el-main>
        <el-container v-if="!isPost">
          <el-container>
            <el-main class="main-page">
              <router-view />
            </el-main>
            <el-footer></el-footer>
          </el-container>
          <el-aside style="width: 27.5%;">
            <home-side/>
          </el-aside>
        </el-container>
        <router-view v-else/>
      </el-main>
      <el-footer>
        <foot/>
      </el-footer>
    </el-container>
  </el-container>
</template>
<script>
import Foot from './Foot.vue'
import Side from './Side.vue'
import HomeSide from '../home/Side.vue'
import TopBar from './TopBar.vue'
export default {
  name: 'basicDesktop',
  components: { Side, TopBar, Foot, HomeSide },
  computed: {
    isPost () {
      return this.$route.name === this.routeName.post
    }
  }
}
</script>
<style>
.main-page{
  margin-left: 3.3rem;
  padding: 0px 20px;
}
.side{
  height: 59.12rem;
  width: 20.3rem;
}
</style>



